@import url('https://fonts.googleapis.com/css2?family=Fraunces:opsz,wght@9..144,700&family=Montserrat:wght@500&display=swap');
.card-container{
    width: 55%;
    height: 60vh;
    display: flex;
    justify-content: center;
}
.image-card{
    width: 50%;
    height: auto;
}
.image-card img{
    width: 100%;
    height: 100%;
    border-radius: 15px 0px 0px 15px;
    object-fit: cover;
}
.description-container{
    width: 50%;
    height: auto;
    background-color:hsl(0, 0%, 100%);
    border-radius: 0px 15px 15px 0px;
    padding: 0px;
    display: flex;
    justify-content: center;
}
.description{
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
.product-name{
    font-size: 1.05rem;
    font-family: 'Montserrat';
    font-weight: 500;
    letter-spacing: 8px;
    color: hsl(228, 12%, 48%);
}
.title{
    font-size: 2.8rem;
    font-weight: 700;
    font-family: 'Fraunces', serif;
    line-height: 50px;
}
.paragraph{
    word-spacing: 2px;
    font-size: 1.09rem;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    line-height: 28px;
    color: hsl(228, 12%, 48%);
}
.price-contain{
    width: 100%;
    display: flex;
}
.price{
    font-size: 2.1rem;
    color: hsl(158, 36%, 37%);
    margin: 5px;
    font-family: 'Fraunces';
    font-weight: 800;
}
.discount{
    font-size: 1.2rem;
    margin: 5px;
    color: hsl(228, 12%, 48%);
}
.button-contain{
    width: 100%;
    display: flex;
    justify-content: center;
}
.button-cart{
    width: 98%;
    height: 60px;
    border-radius: 8px;
    background-color:hsl(158, 36%, 37%);
    border: none;
    text-align: center;
    color: white;
    font-size: 1.09rem;
    font-weight: 500;
    font-family: 'Montserrat';
}
.button-cart svg{
    margin-right: 10px;
}

@media screen and (max-width: 1140px){
    .card-container{
        width: 90%;
        height: 95vh;
        display: block;
    }
    .image-card{
        width: 100%;
        height: 40vh;
    }
    .image-card img{
        border-radius: 15px 15px 0px 0px;
        object-fit: cover;
    }
    .description-container{
        width: 100%;
        height: 55vh;
        border-radius: 0px 0px 15px 15px;
    }
    .description{
        width: 90%;
    }
    .product-name{
        font-size: 1rem;
    }
    .title{
        font-size: 2rem;
        line-height: 40px;
    }
    .paragraph{
        font-size: 1rem;
        line-height: 25px;
    }
    .price-contain{
        width: 100%;
        display: flex;
    }
    .price{
        font-size: 1.8rem;
    }
    .discount{
        font-size: 1rem;
    }
}